<template>
  <div
    class="jumbotron jumbotron-fluid rounded bg-white border-0 shadow-sm border-left px-4"
  >
    <div class="container">
      <h2 align="center" class="my-4">Categories</h2>

      <button @click="addCategory" class="btn btn-primary">Add Category</button>

      <div class="row mt-3">
        <CategoryCard v-for=" (category, index) in categories" :key="index"
          @delete="deleteCategory"
          @edit="editCategory"
          :categoryIndex="index"
          :name="category.name"
          :img="category.img"
        />
      </div>

    </div>
    
  </div>
  <Modal v-if="showModal" @close="closeModal" :title="modalTitle" :danger="dangerModal">
    <template v-slot:body>
      <div class="form-group">
        <label>Category Name</label>
        <input type="text" class="form-control" v-model="name" />
      </div>
      <div class="form-group">
        <label>Category Image</label>
        <input type="text" class="form-control" v-model="img" />
      </div>
    </template>
    <template v-slot:footer>
      <input
        type="button"
        class="btn btn-dark mx-1 btn-xs"
        value="Cancel"
        @click="closeModal"
      />
      <input
        type="button"
        class="btn btn-success btn-xs"
        :value="modalActionButton"
        @click="saveChanges"
      />
    </template>
  </Modal>

  <Modal v-if="showDeleteModal" @close="closeDeleteModal" :title="modalTitle" :danger="dangerModal">
    <template v-slot:body>
      <p align="center"> Do you really wanna remove this category ?</p>
    </template>
    <template v-slot:footer>
      <input
        type="button"
        class="btn btn-dark mx-1 btn-xs"
        value="No"
        @click="closeDeleteModal"
      />
      <input
        type="button"
        class="btn btn-danger btn-xs"
        value="Yes"
        @click="acceptDelete"
      />
    </template>
  </Modal>
</template>

<script>
import CategoryCard from "../../components/CategoryCard.vue";
import axios from "axios";
import Modal from "../../components/Modal.vue";
export default {
  name: "Categories",
  components: { CategoryCard, Modal },
    created() {
      axios
        .get("categories")
        .then( res => this.categories = res.data )
        .catch( err => console.log(err) )
    },
  data() {
    return {
      categories: [],
      name: "",
      img: "",
      categoryId: "",
      modalActionButton: "",
      modalTitle: "",
      showModal: false,
      showDeleteModal: false,
      dangerModal : false
    }
  },
  methods: {
    closeModal() {
      this.showModal = false
    },
    closeDeleteModal() {
      this.showDeleteModal = false
    },
    addCategory() {
      this.categoryId = ''
      this.name = ''
      this.img = ''
      this.modalActionButton = 'Add'
      this.modalTitle = 'Add Category'
      this.showModal = true
      this.dangerModal = false
    },
    editCategory(categoryIndex) {
      this.categoryIndex = categoryIndex
      this.name = this.categories[categoryIndex].name
      this.img = this.categories[categoryIndex].img
      this.modalActionButton = 'Save'
      this.modalTitle = 'Edit Category'
      this.showModal = true
      this.dangerModal = false
    },
    deleteCategory(categoryIndex) {
      this.categoryIndex = categoryIndex
      this.modalTitle = 'Remove Category'
      this.dangerModal = true
      this.showDeleteModal = true
    },
    acceptDelete() {
        axios
        .delete("categories/" + this.categories[this.categoryIndex].id)
        .then( _ => {
          this.categories.splice(this.categoryIndex,1) 
          this.showDeleteModal = false
        })
        .catch( err => console.log(err) )
    },
    saveChanges() {
      if(this.modalActionButton === 'Save')
      {
        axios
            .put('categories/' + this.categories[this.categoryIndex].id,{ 
              name: this.name, 
              img: this.img 
            })
            .then( res => {
              this.categories[this.categoryIndex] = res.data 
              this.showModal = false
            })
            .catch( err => console.log(err) )
      } else{
            axios
            .post('categories',{
              name : this.name,
              img : this.img
            })
            .then( (res) => {
              this.categories.push(res.data) 
              this.showModal = false
            })
            .catch( err => console.log(err) )
      }
    }
  },
}
</script>
