<template>
  <div class="col-md-3 col-sm-6">
    <div class="card product mb-4 box-shadow">

      <img class="card-img-top" :src="`../img/${img}`" alt="Category img" />

      <div class="card-body">
        <h5 class="card-title text-center">{{ name }}</h5>
        <div class="d-flex justify-content-center align-items-center" style="gap: 8px;">
          <div class="btn-group">
            <button
              @click="$emit('edit',this.categoryIndex)"
              type="button"
              class="btn btn-sm text-white btn-success"
            > Edit
            </button>
          </div>
          <div class="btn-group">
            <button
              @click="$emit('delete',this.categoryIndex)"
              type="button"
              class="btn btn-sm text-white btn-danger"
            > Delete
            </button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "CategoryCard",
  props: {
    categoryIndex: Number,
    img: String,
    name: String,
  },
};
</script>
